import React from 'react'
import { Button, Row, Col, Tab, Nav } from 'react-bootstrap'

const UserDefault = ({ setUserPageState, userData }) => {
    
    return (
        <div className='mt-5'>

            {userData['userType'] === "admin" &&
                <Row className="justify-content-md-center" md="auto"    >
                    <Col>

                        <>
                            <Button variant="primary" size="sm" className='ms-3 me-1' onClick={() => { setUserPageState("testVideo"); }}>
                                Generate Videos (1 image)
                            </Button>

                            <Button variant="primary" size="sm" className='me-4 ms-1' onClick={() => { setUserPageState("compareTests"); }}>
                                Results
                            </Button>

                            <Button variant="primary" size="sm" className='ms-4 me-1' onClick={() => { setUserPageState("testVideoDouble"); }}>
                                Generate Videos (2 images)
                            </Button>

                            <Button variant="primary" size="sm" className='me-3 ms-1' onClick={() => { setUserPageState("compareTestsDouble"); }}>
                                Results
                            </Button>

                        </>

                    </Col>
                </Row>
            }

            <Row className="justify-content-md-center mt-4" md="auto"    >

                {/* <Col xs lg="6">My feeds:

                    {userData.userFeeds &&
                        userData.userFeeds.map((feed, i) =>

                            <div key={i}>{i}: {feed[2]}</div>
                        )}
                </Col>
                <Col></Col> */}


            </Row>
            


        </div>


    )
}

export default UserDefault