import React, { useEffect, useState } from 'react';
import TestVideo from '../components/TestVideo';
import TestVideoDouble from '../components/TestVideoDouble';

import CompareTests from '../components/CompareTests';
import CompareTestsDouble from '../components/CompareTestsDouble';

import { Button, Row, Col } from 'react-bootstrap'
import LoadingModal from '../components/LoadingModal'
import UserDefault from '../components/UserDefault';
import {logout} from '../utils.js'


const UserPage = ({ email, userPageState, setUserPageState, isGenerating, setIsGenerating,
    testVideoData, setTestVideoData, testVideoDoubleData, setTestVideoDoubleData, setToastShow }) => {

    // const [userPageState, setUserPageState] = useState("")
    const [userData, setUserData] = useState({"userType": "", "userFeeds": ""})
    
    const [loadingShow, setLoadingShow] = useState(false);
    
    
    const main = (userPageState) => {
        switch (userPageState) {

            case "compareTests":
                return <CompareTests setUserPageState={setUserPageState} className='mt-4' />

            case "compareTestsDouble":
                return <CompareTestsDouble setUserPageState={setUserPageState} className='mt-4' />

            case "testVideo":
                return <TestVideo setUserPageState={setUserPageState} className='mt-4' isGenerating={isGenerating} setToastShow={setToastShow}
                setIsGenerating={setIsGenerating}  testVideoData={testVideoData} setTestVideoData={setTestVideoData}/>
            case "testVideoDouble":
                return <TestVideoDouble setUserPageState={setUserPageState} className='mt-4' isGenerating={isGenerating} setToastShow={setToastShow}
                setIsGenerating={setIsGenerating}  testVideo2={testVideoDoubleData} setTestVideo2={setTestVideoDoubleData}/>
            default:
                return <UserDefault setUserPageState={setUserPageState} className='mt-4' isGenerating={isGenerating} 
                setIsGenerating={setIsGenerating} email={email} userData={userData}/>
        }
    }
    useEffect(() => {
        async function getUser() {
            setLoadingShow(true)

            let res = await fetch(`https://shoevideo.azurewebsites.net/api/get_user_info`, {
                method: "POST",
                body: JSON.stringify({
                    'user_token': sessionStorage.getItem('userToken'),
                }),
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'x-functions-key': sessionStorage.getItem('functions-key'),
                },
            })

            let resData = await res.json();
            if (res.ok) {
                setUserData({"userType": resData["user_type"], "userFeeds": resData["user_feed_data"]})
                setLoadingShow(false)
            }
            else {
                setLoadingShow(false)
                // logout()

            }
        }
        getUser();

    }, []);

    return (
        <div>
            <LoadingModal show={loadingShow} text={'Getting user data'} />
            {main(userPageState)}
        </div >
    );
};
export default UserPage;