// import './App.css';
import React, { useState } from 'react'
import { logout } from './utils.js'
import './dots.css'; 
import { Row, Col} from 'react-bootstrap'


import {
  // BrowserRouter as Router,
  HashRouter as Router,
  Routes,
  Route
} from "react-router-dom";
import Footer from './components/Footer'
import Header from './components/Header';
import MainPage from './pages/MainPage';
import CreateVideosPage from './pages/CreateVideosPage';
import CreateFeedPage from './pages/CreateFeedPage';
import LoginPage from './pages/LoginPage';
import UserPage from './pages/UserPage';
import MyFeedsPage from './pages/MyFeedsPage';

import { Toast } from 'react-bootstrap'


const checkAuth = () => {
  if (sessionStorage.getItem('functions-key') && sessionStorage.getItem('userToken')) return true;

  return false
};

const getEmail = () => {
  const emailString = sessionStorage.getItem('email');
  return emailString
};

const saveUserData = (funcKey, email, jwt) => {
  sessionStorage.setItem('functions-key', funcKey);
  sessionStorage.setItem('email', email);
  sessionStorage.setItem('userToken', jwt);
};


function App() {
  const [userPageState, setUserPageState] = useState("")
  const [isAuth, setIsAuth] = useState(checkAuth())
  const [email, setEmail] = useState('')
  const [isGenerating, setIsGenerating] = useState(false)

  const [testVideoData, setTestVideoData] = useState({ "imageType": "", "imageData": "", "videoData": false, "checked": true, "message": false })
  const [testVideoDoubleData, setTestVideoDoubleData] = useState({ "imageUrl1": "", "imageUrl2": "", "videoData": false, "checked": true, "message": false })

  const [toastShow, setToastShow] = useState(false)


  if (!isAuth) {
    return (
      <LoginPage email={email} setEmail={setEmail} saveUserData={saveUserData} setIsAuth={setIsAuth} />
    )
  }

  else {
    return (
      <>

        <Router>
          <Header email={getEmail()} logout={logout} setUserPageState={setUserPageState} />

          <Routes >
            <Route path="" element={<MainPage />} />
            <Route path="createFeed" element={<CreateFeedPage />} />

            <Route path="createVideos" element={<CreateVideosPage />} />
            <Route path="user" element={<UserPage email={getEmail()} testVideoData={testVideoData} setTestVideoData={setTestVideoData}
              setUserPageState={setUserPageState} userPageState={userPageState} isGenerating={isGenerating} setIsGenerating={setIsGenerating}
              testVideoDoubleData={testVideoDoubleData} setTestVideoDoubleData={setTestVideoDoubleData} setToastShow={setToastShow} />} />
          
            <Route path="myFeeds" element={<MyFeedsPage />} />
            
          </Routes>

          <Toast onClose={() => setToastShow(false)} show={toastShow} delay={5000} autohide bg="light" className="m-2"
            style={{ position: "fixed", bottom: "0", right: "0" }}>
            <Toast.Header>
              <strong className="me-auto text-success">Success</strong>
              <small></small>
            </Toast.Header>
            <Toast.Body className="text-success">Video generated successfully!</Toast.Body>
          </Toast>

          {/* <Footer className="footer" style={{ position: "absolute", bottom: "0px" }} /> */}

        </Router>
        

        {isGenerating &&
          <div className='p-1 btn btn-outline-primary disabled m-2' style={{ position: "fixed", bottom: "0", right: "0", height: "40px"}}>
            
          <Row>
            <Col xs="9">
              <span style={{"fontSize": "16px"}}>Generating video</span>
            

            </Col>
            <Col xs="2">
            <div className="dot-windmill mt-2"></div>

            </Col>
            <Col xs="1">
            </Col>

          </Row>
          
        </div>
        }

      </>

    );
  }
}

export default App;
