import React from 'react'
import { Button } from 'react-bootstrap'
import {getPreviewVideo} from '../utils.js'

const ArrangeVisuals = ({ setCreateFeedState, setAlertState, feedData, setData, token }) => {
    const handleClick = async (e) => {
        setCreateFeedState("loading")
        
        let resp = await getPreviewVideo(feedData)
        if(resp.stat){
            feedData['previewVideoUrl'] = resp.url
            setData(feedData)
            setCreateFeedState("preview")
        }
        else{
            setCreateFeedState("arrangeFeed")
            setAlertState({ show: true, text: resp.detail })
        }


    }
    return (
        <div>


            <p> Pick a theme </p>
            <p> Pick a color etc...</p>
            <Button variant="primary" onClick={handleClick}>Continue</Button>

        </div>
    )
}

export default ArrangeVisuals
