import LoadingModal from '../components/LoadingModal';
import React, { useEffect, useState } from 'react'
import { Button, Row, Col, ListGroup } from 'react-bootstrap'

const MyFeedsPage = () => {
    const [loadingShow, setLoadingShow] = useState(false);
    const [userFeedsData, setUserFeedsData] = useState(false);
    const [currentFeed, setCurrentFeed] = useState(0);

   
    useEffect(() => {
        async function getUserFeeds() {
            setLoadingShow(true)

            let res = await fetch(`https://shoevideo.azurewebsites.net/api/get_user_feed_data`, {
                method: "POST",
                body: JSON.stringify({
                    'user_token': sessionStorage.getItem('userToken'),
                }),
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'x-functions-key': sessionStorage.getItem('functions-key'),
                },
            })

            let resData = await res.json();
            if (res.ok) {
                setUserFeedsData(resData['user_feed_data'])
                console.log(resData['user_feed_data']);
                setLoadingShow(false)
            }
            else {
                setLoadingShow(false)
            }
        }
        getUserFeeds();

    }, []);
    return (
        <>
            <LoadingModal show={loadingShow} text={'Getting user feed data.'} />


            <Row className="justify-content-md-center mt-4" md="auto"    >

                <Col xs lg="2">

                    <ListGroup>
                        <ListGroup.Item >
                            <strong>My Feeds: </strong>
                        </ListGroup.Item>

                        {userFeedsData &&
                            userFeedsData.map((feed, i) =>
                                <ListGroup.Item key={i} action active={currentFeed == i ? "true" : ""}
                                 onClick={() => { setCurrentFeed(i); console.log("i", i) }}>

                                    <span key={i} > {feed['feed_name']}</span>

                                </ListGroup.Item>

                            )}

                    </ListGroup>
                </Col>
                <Col xs lg="6">
                        {userFeedsData && 
                        <>
                        {userFeedsData[currentFeed]['feed_id']}
                            
                        {Object.values(userFeedsData[currentFeed]).map((element, i) =>
                                    <div key={i}>

                                        {element.toString().length < 50 ? element.toString() : element.toString().substring(0, 50) + '...'}</div>
                                )}
                        </>}
                </Col>


            </Row>
        </>
    )
}

export default MyFeedsPage