import React from 'react'
import { Container, Row, Col, Carousel } from 'react-bootstrap'



const MainPage = () => {
    return (
        <div>
            <Container>
                <Row className="justify-content-md-center">
                    <Col lg="8">
                        <div className='mt-4 fs-4'>
                            {/* <strong >Main Page</strong>  */}


                            <Carousel variant="dark" className="w-100" style={{  backgroundColor: 'white'}}>
                                <Carousel.Item style={{ height: '360px' }}>
                                <Row className='gx-0'>
                                    <Col xs lg="6">
                                        <img src="https://videoappex.blob.core.windows.net/video-test/5319bd07-be0a-45d0-9fc6-cdbdf482f7ee" style={{ width: "100%" }} alt=""/>

                                    </Col>
                                    <Col xs lg="6">
                                        <video width="100%" autoPlay loop>
                                            <source src="https://videoappex.blob.core.windows.net/video-container/0b45fb2f-82bb-4e60-927f-d87913ce0ba9" type="video/mp4"></source>
                                        </video>
                                    </Col>
                                </Row>
                                    <Carousel.Caption>
                                        {/* <h5>First slide label</h5> */}
                                        <p>Generate videos from single image.</p>
                                    </Carousel.Caption>
                                </Carousel.Item>
                                <Carousel.Item style={{ height: '360px', backgroundColor: 'white'}}>
                                    
                                    <Row className='gx-0 mt-4'>
                                    
                                    <Col xs lg="4">
                                        <img src="https://productimages.hepsiburada.net/s/178/1500/110000143052193.jpg" style={{ maxHeight: "100%", maxWidth: "100%" }} alt=""/>

                                    </Col>
                                    <Col xs lg="4">
                                        <img src="https://productimages.hepsiburada.net/s/178/1500/110000143052194.jpg" style={{ maxHeight: "100%", maxWidth: "100%" }} alt=""/>

                                    </Col>
                                    <Col xs lg="4">
                                        <video width="100%" autoPlay loop>
                                            <source src="https://videoappex.blob.core.windows.net/video-container/9673568c-d917-4d1d-a5dc-abe3f55cd760" type="video/mp4"></source>
                                        </video>
                                    </Col>
                                </Row>
                                    <Carousel.Caption>
                                        {/* <h5>Second slide label</h5> */}
                                        <p>Generate video from a image pair.</p>
                                    </Carousel.Caption>
                                </Carousel.Item>
                                <Carousel.Item style={{ height: '360px' }}>
                                <Row className='gx-0 justify-content-md-center'>
                                    <Col xs lg="6">
                                        <img src="https://www.dignited.com/wp-content/uploads/2021/03/google-sheets.png" style={{ width: "100%" }} alt=""/>

                                    </Col>
                                </Row>
                                    <Carousel.Caption>
                                        {/* <h5>Third slide label</h5> */}
                                        <p>Provide a feed to auto generate videos.</p>
                                    </Carousel.Caption>
                                </Carousel.Item>
                            </Carousel>
                        </div>
                    </Col>
                </Row>

            </Container>
        </div>
    )
}

export default MainPage
